import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Feed from "./Feed"

const News = ({startDate, endDate}) => {

  const data = useStaticQuery(graphql`
  {
    allFeedNews {
      nodes {
        content
        guid
        pubDate
        title
        link
      }
    }
  
  }
`)

return (
  <>
     <Feed title="News" startDate={startDate} endDate={endDate} nodes={data.allFeedNews.nodes} link="/news" archive="/" source=""/>   
  </>
  )
}
export default News